import { fromEvent, Observable, timer } from 'rxjs';
import { debounce, map, switchMap } from 'rxjs/operators';
import 'bootstrap/dist/css/bootstrap.min.css';

interface Card {
  full_name: string;
  description: string;
  owner: {
    avatar_url: string;
  };
}

interface Cards {
  items: Card[];
}

const searchInput: Element = document.querySelector('input[name="search"]') as Element;

function request(str: string): Promise<any> {
  const newUrl: URL = new URL('https://api.github.com/search/repositories');
  newUrl.searchParams.set('q', str);
  return fetch(newUrl.toString())
    .then((res: Response) => res.json());
}

const changeInputText$: Observable<Cards> = fromEvent(searchInput, 'input')
  .pipe(
    debounce(() => timer(500)),
    map((event: Event) => (event as any).target.value),
    switchMap((v: string) => request(v))
  );

  changeInputText$.subscribe(
    (v: Cards) => {
      renderCards(v.items);
    }
  );

const removeChildrenElement: Function = (node: HTMLElement) => {
  if (node.hasChildNodes()) {
    const children: any = node.childNodes;
    for (let i: number = children.length - 1; i >= 0; i -= 1) {
      children[i].remove();
    }
  }
};

const renderCards: Function = (cards: Card[]) => {
  const cardsList: HTMLElement = (document.getElementById('cards_list') as HTMLElement);
  removeChildrenElement(cardsList);

  const cardTemplateElement: HTMLElement = (document.getElementById('card-template') as any).content;
  const fragment: DocumentFragment = document.createDocumentFragment();

  cards.forEach((card: Card) => {
    const node: any = cardTemplateElement.cloneNode(true);
    const img: any = node.querySelector('.card-img-top');
    const title: HTMLElement = node.querySelector('.card-title');
    const description: HTMLElement = node.querySelector('.card-text');

    img.src = card.owner.avatar_url;
    title.textContent = card.full_name;
    description.textContent = card.description;
    fragment.appendChild(node);
  });
  cardsList.appendChild(fragment);
};

